.card{
  width: 91.20%;
  background-color: #ffff;
  border-radius: 10px;
  overflow: hidden;

  font-size: 0.875rem;
}

.card__body{
  padding: 25px;
  color: var(--dark-grayish-blue);
}

.card__image--mobile {
  width: 100%;
}

.card__image--desktop {
  display: none;
}

.card__category{
  text-transform: uppercase;
  
  font-size: 0.625rem; 
  letter-spacing: 6px;
}

.card__title{
  margin: 0.875em 0;
  font-family: 'Fraunces', serif;
  color: var(--very-dark-blue);
  font-size: 2rem;
  font-size: 700;
  line-height: 1em;
}

.card__price{
  margin-bottom: 22px;

  display: flex;
  align-items: center;
}

.card__discounted-price{
  color: var(--dark-cyan);
  font-family: 'Fraunces', serif;
  font-size: 2rem;
  margin-right: 22px;
}

.card__original-price{
  text-decoration: line-through;
}

.card__add-to-cart-button{
  font-family: 'Montserrat', sans-serif;
  color: white;
  width: 100%;
  background-color: var(--dark-cyan);
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 700;
  padding: 1em 0;
  cursor: pointer
}

.card__add-to-cart-icon{
  width: 20px;
  margin-right: 10px;
}

.card__add-to-cart-button > span {
  text-align: center;
  vertical-align: middle;
}

.card__add-to-cart-button:hover{
  background-color: #1A4031;
}

@media screen and (min-width: 800px) {
  .card{
    max-width: 545px;
    display: grid;
    grid-template-columns: 50% 50%;
  }

  .card__figure{
    height: 100%;
  }

  .card__image--mobile {
    display: none;
  }

  .card__image--desktop {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }
}